.title__inner {
    color: $green-dark;
    font-family: 'Raleway-Bold';
    margin-bottom: .8em;

}

.img__cover__inner {
    margin-top: -70px;
    width: 65%;
    float: right;


    @media (max-width:767.9px) {

        float: none;
        width: 300px;
        margin: 10px auto;

    }


}

.form__find {
    input {
        width: 400px !important;
        height: 55px;

        @media (max-width: 575.9px) {
            height: 55px;
            width: 275px !important;
        }
    }

    button {
        width: 130px !important;

        @media (max-width: 575.9px) {
            height: 55px;
            width: 100px !important;
            margin-left: -65px;
        }


    }
}

.profile-img {
    width: 100px;
    height: 100px;
    @include border-radius(50%);
    overflow: hidden;
    border: 5px solid rgba(227, 227, 227, .66);

    img {
        width: 100%;
        height: 100%;
        @include border-radius(50%);


    }
}

.rate {
    span {
        width: 15px;
        height: 15px;
        display: inline-block;
        background: url(../images/svgs/ico-rate.svg)no-repeat;
        background-size: contain;

        &.checked {
            background-image: url(../images/svgs/ico-rated.svg);

        }
    }
}


.form__filter {

    .inputs__select {

        @media (max-width: 575.9px) {

            width: 53%;

        }
    }

    select.form-control {
        border: 1px solid #E0E0E0;
        padding-right: 20px;
        background-color: transparent;
        background-image: url(../images/svgs/arr-select-green.svg);
        background-position: right 8px center;
        background-repeat: no-repeat;

        width: 150px;
        margin-right: 15px;
        margin-bottom: 10px;

        @media (min-width: 1200px) {

            margin-bottom: 0px;

        }

        @media (max-width: 575.9px) {

            width: 100%;

        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.switch {
    flex: 0 0 120px;

    label {
        line-height: 35px;
    }
}

.item__three__diff {

    @include border-radius(15px);
    background: #fff;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    position: relative;

    .cover_person {
        width: 100%;
        height: 88px;
        position: relative;
        overflow: hidden;

        .cover {
            width: 100%;
            min-height: 100%;
        }

        .overlay-cover {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: rgba(30, 137, 145, .92);
            @include border-top-right-radius(15px);
            @include border-top-left-radius(15px);

        }



    }

    .profile-img {
        position: absolute;
        top: 25px;
        left: 35px;
        overflow: hidden;


    }

    .data-person {
        padding: 10px 25px 0;
        margin-top: 40px;

        @media (max-width: 991.9px) {

            padding: 15px 5px;


        }

        .name__item {
            font-size: 1.3em;


        }




    }



    .link-view {
        padding: 15px 0;
        text-align: center;
        border-top: 1px solid rgba(112, 112, 112, .1);
        font-family: 'Raleway-Medium';
        color: $green-dark;

        a {
            color: inherit;

            svg {
                margin-left: 10px;
            }
        }
    }

    &.green {
        .overlay-cover {
            background: rgba(96, 224, 143, .92);
        }

        .link-view {
            color: #60E08F;
        }

        svg {
            path {
                fill: #60E08F;

            }
        }
    }

    &.blue-light {
        .overlay-cover {
            background: rgba(0, 188, 212, .92);
        }

        .link-view {
            color: #00BCD4;


        }

        svg {
            path {
                fill: #00BCD4;

            }
        }
    }

    .btn-pos {
        position: absolute;
        top: 20px;
        right: 20px;
        z-index: 5;
        border-width: 2px;

        @media (max-width: 991.9px) {

            right: 5px;


        }

    }
}

.info__item {
    color: #9C9CA8;
    font-family: 'Raleway-Medium';



    span {
        margin-bottom: 10px;
        font-size: .9em;

        &.t-job {
            font-size: 1em;
        }
    }

    svg {
        width: 15px;
        height: 15px;
        display: inline-block;
        margin-right: 5px;

        /* &.ico-pin {

                    background: url(../images/svgs/ico-pin.svg)no-repeat;
                    background-size: contain;

                }

                &.ico-price {
                    width: 17px;
                    height: 17px;
                    background: url(../images/svgs/ico-price.svg)no-repeat;
                    background-size: contain;


                }*/
    }


}

.btn-follow {

    color: #fff;
    width: 100px;
    height: 35px;
    border-radius: 20px;

    @media (max-width: 991.9px) {

        width: 88px;
        height: 32px;


    }

    @media (max-width: 575.9px) {

        margin: 0 auto;
        display: block;


    }

}

.sort {
    select {
        font-family: 'Raleway-Bold';
        color: $green-dark;
        border: 0
    }
}

.content-list {
    background: #F9FCFD;
    border: 1px solid #E4F3F8;
    padding: 20px 0;
    @include border-radius(15px);
}

.view-full {
    box-shadow: none;
    background: transparent;

    .item {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid rgba(112, 112, 112, .28);
        overflow: hidden;

        &:last-child {
            border-bottom: 0;
        }

    }

    .profile-img {
        position: relative;
        top: auto;
        left: auto;

        @media (max-width: 575.9px) {

            margin: 0 auto;


        }
    }

    .data-freelancer {
        margin-left: 100px;

        @media (max-width: 575.9px) {

            margin-left: 0;


        }

        .data-person {
            flex: 0 0 84%;
            margin-top: 0;

            @media (max-width: 991.9px) {

                flex: 0 0 75%;


            }
        }
    }

    .view-profile {
        .btn {
            width: 140px;
            height: 40px;
            @include border-radius(25px);

        }
    }


}

.skills {
    li {

        display: inline-flex;
        align-items: center;
        position: relative;

        @media (max-width: 991.9px) {

            margin-bottom: 5px;


        }

        &.skills-item {
            background: $green-dark;
            color: #fff;
            padding: 7px 10px;
            margin-right: 30px;
            margin-bottom: 5px;

            @media (max-width: 575.9px) {

                margin-right: 20px;



            }

            &:after {
                content: "";
                position: absolute;
                top: 0;

                right: -15px;
                width: 0;
                height: 0;
                border-top: 19px solid transparent;
                border-bottom: 16px solid transparent;
                border-left: 15px solid $green-dark;

                @media (max-width: 767.9px) {

                    border-bottom: 14px solid transparent;


                }

                @media (max-width: 575.9px) {

                    border-bottom: 12px solid transparent;


                }
            }

            i {
                display: inline-block;
                margin-right: 8px;
                width: 8px;
                height: 8px;
                @include border-radius(50%);
                background: #fff;
            }
        }

        &.more__link {
            color: $green-dark;
            font-size: 1.2em;
            font-weight: bold;
            border-bottom: 1px solid $green-dark;
        }




    }


}

.jobs-list {
    .item {
        position: relative;
    }

    .desc-job {
        width: 70%;
    }

    .info__item {
        span {
            margin-right: 20px;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    .t-prop {
        img {
            width: 20px;
            margin-right: 5px;
        }
    }
}

.job-review {
    position: absolute;
    top: 0;
    right: 0;

    .ico {
        width: 50px;
        height: 50px;
        @include border-radius(50%);
        display: inline-block;

        &.ico-like {
            background: url(../images/svgs/ico-like.svg)no-repeat;
            background-size: contain;
        }

        &.ico-dislike {
            background: url(../images/svgs/ico-dislike.svg)no-repeat;
            background-size: contain;
        }
    }
}

.form-inline-custom {
    select {
        display: inline-block;
    }

    .multi-select-button {
        border: 1px solid #E0E0E0;
        padding-right: 20px;
        background-color: transparent;
        background-image: url(../images/svgs/arr-select-green.svg);
        background-position: right 5px center;
        background-repeat: no-repeat;
        width: 200px;
        margin-right: 15px;
        margin-bottom: 10px;
        height: calc(3rem + 2px);
        padding: .375rem 1.75rem .375rem .75rem;
        border-radius: .5rem;
        line-height: 32px;
        font-size: 1em;
        background-size: 10px;

        @media (min-width: 1200px) {
            margin-bottom: 0px;
        }
    }
}