html {
  font-size: 14px;
}

body {
  font-size: 1rem;
  color: $black-light;
  background: $white;
  overflow-x: hidden;

  @media (max-width: 767.9px) {
    font-size: 0.9rem;
  }

  @media (max-width: 575.9px) {
    font-size: 0.8rem;
  }
}

.datepicker,
.datepicker-container {
  z-index: 1600 !important;
  /* has to be larger than 1050 */
}

.ui-datepicker {
  z-index: 1600 !important;
  /* has to be larger than 1050 */
}

.c-black {
  color: $black;
}

.c-black-light {
  color: $black-light;
}

h1 {
  font-size: 3em;
}

h3 {
  font-size: 2.6em;
}

h5 {
  font-size: 1.9em;
}

h6,
.h6 {
  font-size: 1.3em;
}

a {
  &:hover {
    text-decoration: none;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.mb__section {
  margin-bottom: 7em;

  @media (max-width: 767.9px) {
    margin-bottom: 50px;
  }
}

.breadcrumb {
  background: transparent;
}

.mt__inner {
  margin-top: 80px;
}

img {
  max-width: 100%;
}

.h-100vh {
  height: 100vh;
}

.btn {
  font-size: 1em;
  @include border-radius(15px);

  &.btn-lg {
    padding: 0.8em 1.1em;
    width: 185px;
    @include border-radius(22px);
  }

  &.btn-md {
    width: 150px;
  }

  &.btn-small {
    width: 120px;
    padding: 0.7em 1.1em;
    border-radius: 20px;
  }

  &.btn-green {
    background: $main-gradient;
    color: $white;
  }

  &.btn-white {
    background: $white;
    color: $green-dark;
    border: 1px solid $green-light;
    position: relative;
    transform: perspective(1px) translateZ(0);

    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: $green-dark;
      border: 1px solid $green-dark;
      z-index: -1;
      transform: scale(0);
      @include border-radius(20px);

      transition: all 0.5s ease-in-out 0s;

      transition-timing-function: ease-out;
    }

    &:hover {
      color: $white;

      &:after {
        transform: scale(1);
      }
    }

    &.green-txt-btn {
      border: 1px solid $green-light;
      color: $green-light;

      &:hover {
        color: $white;
      }
    }
  }

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(98, 183, 150, 0.25);
  }
}

.bg-green {
  background: $green-light;
}

.green-light {
  color: $green-light;
}

.green-dark {
  color: $green-dark !important;
}

.btn-border-green {
  border-color: $green-dark !important;
  color: $green-dark !important;
}

.no-raduis {
  .form-control {
    @include border-radius(0);
  }
}

h2 {
  @media (max-width: 575.9px) {
    font-size: 2em;
  }
}

.heading__section {
  h2 {
    font-family: "Raleway-ExtraLight";
    color: $black;

    span {
      font-family: "Raleway-Bold";
    }

    @media (max-width: 767.9px) {
      text-align: center;
    }
  }
}

.c-green-dark {
  color: $green-dark !important;
}

.c-green-light {
  color: $green-light !important;
}

.font__bold {
  font-family: "Raleway-Bold";
}

.mb-40 {
  margin-bottom: 40px;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  color: #495057;
  background-color: transparent;
  border-color: transparent;
}

.btn-w-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  .ico-arrow {
    background: url(../images/pngs/arrow-btn.png) no-repeat center;
    background-size: contain;
    width: 17px;
    height: 17px;
    display: inline-block;
    margin-left: 10px;
  }

  .ico-arrow-green {
    background-image: url(../images/svgs/arrow-btn-green.svg);
  }

  .ico-true {
    background: url(../images/pngs/ico-true.png) no-repeat center;
    background-size: contain;
    width: 12px;
    height: 17px;
    display: inline-block;
    margin-left: 10px;
  }
}

#loader-wrapper {
  .loader-section {
    position: fixed;
    top: 0;
    width: 51%;
    height: 100%;
    background: $green-dark;
    z-index: 1000;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);

    &.section-left {
      left: 0;
    }

    &.loader-section.section-right {
      right: 0;
    }
  }
}

.loaded {
  #loader-wrapper {
    .loader-section {
      &.section-left {
        -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
        transform: translateX(-100%);
        -webkit-transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      }

      &.section-right {
        -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
        transform: translateX(100%);
        -webkit-transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      }
    }
  }
}

/* Transitions */

.animate {
  -moz-opacity: 0;
  -khtml-opacity: 0;
  -webkit-opacity: 0;
  opacity: 0;
  filter: alpha(opacity=0 * 100);
}

.animated {
  -moz-opacity: 1;
  -khtml-opacity: 1;
  -webkit-opacity: 1;
  opacity: 1;
  filter: alpha(opacity=1 * 100);
}

.scale-up-hor-left {
  -webkit-animation: scale-up-hor-left 0.7s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-hor-left 0.7s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

