
.create-circle{
    .container-upload-img {
        width: 20px;
        height: 20px;
        @include border-radius(10px);
        background: rgba(30, 137, 145, .13);
        justify-content: center;
        border: 1px dashed $green-light;
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;
    
        .cover-account {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            text-indent: 10000px;
            cursor: pointer;
        }
    
        .upload {
            color: $green-light;
            font-size: 1.5em;
        }

    }

    .img__wrapper{

        img{
            height: 460px;
            position: relative;
            top: 20px;
            left: 95px;

            @media (max-width: 991.9px) {
                height: 400px;
                top: 100px;
                left: 20px;
                
            }

            @media (max-width: 767.9px) {
                height: 400px;
                top: 0;
                left: 0;

            }

            @media (max-width: 575.9px) {
                height: 250px;
            }
        }
    }
}

.add__bg {
        background-image: url(../images/pngs/Component3.png);
        background-size: 2000px;
        background-repeat: no-repeat;
        background-position: -150px -200px;
        height: 800px;
        padding-top: 50px;

        &:before {
            content: "";
            position: absolute;
            top: 230px;
            left: 310px;
            height: 90px;
            width: 90px;
            background: url(../images/pngs/ico-bg.png) no-repeat;
            background-size: 50%;

        }
}

.make__transparent{
    position: relative;
    z-index: -9999;
}
