.title__page {
    margin-bottom: 60px;
    position: relative;

    &:before {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        background: url(../images/pngs/ico-bg.png) no-repeat;
        background-size: 100%;
        top: -60px;
        left: 20%;
    }
}

.create-wrapper {
    position: relative;
    padding-top: 120px;
    //   background: url(../images/pngs/bg-competion.png)no-repeat right top;

    .cover__page {
        position: absolute;
        z-index: -1;
        top: 0;
        right: 0;
        width: 40%;
        height: 100%;
        background: url(../images/pngs/bg-cover-global.png)no-repeat right top;
        background-size: 100%;

        @media (max-width:767.9px) {

            top: -100px;
        }

        @media (max-width:575.9px) {
            width: 65%;
            top: -70px;
        }

        .person__cover {
            width: 62%;
            height: 100%;
            background: url(../images/pngs/bg-person.png) no-repeat center top;
            background-size: 100%;
            margin: 0 auto;

        }
    }
}


/***************************** List*****************************/
.list__competation {
    li {
        padding-bottom: 20px;
        margin-bottom: 15px;
        border-bottom: 1px solid rgba(112, 112, 112, .28);

        &:last-child {
            border-bottom: 0;
        }
    }

    h6 {
        margin-bottom: 20px;
    }

    p {
        margin-bottom: 40px;
    }

}

.ico-small {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;

    &.ico-categoy {
        background: url(../images/svgs/ico-category.svg)no-repeat center;
        background-size: contain;

    }


    &.ico-calendar {
        background: url(../images/svgs/ico-calendar.svg)no-repeat center;
        background-size: contain;
        width: 15px;
        height: 15px;
    }

    &.ico-sponsor {
        background: url(../images/svgs/ico-sponsor.svg)no-repeat center;
        background-size: contain;
        width: 15px;
        height: 15px;
    }

}

.custom__pagination {
    @extend .lSAction-custom;
}

.competation-details {
    .nav-item {
        margin-right: 6em;

        @media (max-width:575.9px) {
            margin-right: .8em;
        }

        &:last-child {
            margin-right: 0;

        }
    }

    .project-item {

        @media (min-width:768px) {
            width: 30.5%;
        }

        &:nth-child(2n) {

            @media (min-width:768px) {
                margin-right: 3%;
            }
        }

        &:nth-child(3n) {

            @media (min-width:768px) {
                margin-right: 0;
            }
        }
    }
}

.view-comp {

    .reward {
        position: relative;
        margin-top: 40px;

        &:before {
            content: "";
            position: absolute;
            top: -10px;
            left: 10%;
            width: 80%;
            height: 1px;
            background: rgba(112, 112, 112, .28);

        }
    }
}

.material-list {
    li {
        span {
            display: inline-flex;
            color: $green-dark;
            background-image: url(../images/pngs/attach-green.png);
            padding-right: 30px;

            i {
                background: $green-dark;
                border-radius: 50%;
            }
        }
    }
}

.disc__list {
    .img-user {
        position: relative;
        width: 40px;
        height: 40px;
        float: left;

        @media (max-width:575.9px) {
            float: none;
            margin: 0 auto 10px;
        }

        img {
            width: 40px;
            height: 40px;
            @include border-radius(50%);

        }

        .ico-status {
            position: absolute;
            height: 8px;
            width: 8px;
            @include border-radius(50%);
            background: $green-light;
            bottom: 5px;
            right: -9px;

        }
    }

    .item__disc {
        margin-bottom: 30px;


    }

    .disc-data {
        margin-left: 55px;

        @media (max-width:575.9px) {
            margin-left: 0;

        }

        .comment {
            background: #F1F2F2;
            padding: 10px 20px;
            @include border-radius(20px);
            margin-bottom: 15px;

            p {
                margin-bottom: 0;

            }
        }

    }

}

.reactions {
    li {
        display: inline-flex;
        margin-right: 20px;
        align-items: center;
        color: $green-dark;
        cursor: pointer;

        @media (max-width:575.9px) {
            margin-right: 10px;

        }

        &:last-child {
            margin-right: 0;
        }

        .ico {
            width: 15px;
            height: 15px;
            display: inline-block;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            margin-right: 5px;

            &.ico-agree {
                background-image: url(../images/pngs/ico-agree.png);
            }

            &.ico-disagree {
                background-image: url(../images/pngs/ico-disagree.png);

            }

            &.ico-answer {
                background-image: url(../images/pngs/ico-answer.png);

            }

            &.ico-correct {

                background-image: url(../images/pngs/ico-correct.png);

            }

            &.ico-edit {
                width: 20px;
                height: 20px;
                background-image: url(../images/svgs/ico-edit.svg);

            }

        }
    }

}

.up {
    .user-data {
        margin-bottom: 10px;
    }

    .user-name {
        margin-left: 55px;
    }

    .disc-data {
        margin-left: 0 !important;
    }
}

.remain__time {
    ul {
        display: flex;
        justify-content: space-between;
    }

    li {

        span {
            display: block;
            color: $green-dark;

            &:first-child {
                font-size: 2em;
                font-family: 'Raleway-Bold';
            }
        }
    }
}

.competation {
    .project-item .img-project {
        border-bottom: 0;
        margin-bottom: 0;
    }
}