#kh-active-link {
  color: $green-dark;
}

.nav-pills {
  .nav-link {
    background-color: rgba(156, 156, 168, 0.22);
    color: #9c9ca8;
    font-weight: bold;
    padding: 10px 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 991.9px) {
      padding: 10px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    span {
      &:nth-child(2) {
        display: inline-flex;
      }
    }

    .left-icon {
      svg {
        width: 20px;
        height: 18px;
        margin-right: 12px;

        @media (max-width: 991.9px) {
          margin-right: 5px;
        }
      }
    }

    .right-icon {
      width: 20px;
      height: 15px;
      background-size: contain;
      background-repeat: no-repeat;
      display: inline-block;
      background-image: url(../images/svgs/ico-correct-gray.svg);
    }

    &.active {
      @include border-radius(5px);
      background: #fff;
      color: #000;
      box-shadow: 0px 3px 10px rgba(0, 18, 88, 0.1);

      .right-icon {
        background-image: url(../images/svgs/ico-correct-green.svg);
      }

      svg {
        path {
          fill: $green-light;
        }
      }
    }

    &.done {
      background-color: white;
      color: black;

      .right-icon {
        background-image: url(../images/svgs/ico-correct-green.svg);
      }

      svg {
        path {
          fill: $green-light;
        }
      }
    }
  }
}

.create-project {
  h5 {
    font-family: "Raleway-Bold";
    color: #000;
  }

  .btn-next {
    @include border-radius(17px);
    width: 95px;
    height: 35px;
    font-weight: bold;

    .ico-arrow {
      background-image: url(../images/svgs/ico-arrow-w.svg);
      background-size: 14px;
    }
  }

  .btn-back {
    @include border-radius(17px);
    width: 95px;
    height: 35px;

    .ico-arrow {
      background-image: url(../images/svgs/ico-arr-back.svg);
      margin-left: 0;
      margin-right: 10px;
      background-size: 14px;
    }
  }

  .tox .tox-edit-area__iframe {
    background-color: rgba(239, 239, 246, 0.4);
  }

  .tox-tinymce {
    border: 1px solid #efeff6;
  }

  .toolbar__overflow,
  .tox .tox-toolbar__primary {
    background: none;
    background-color: rgba(239, 239, 246, 0.4);
    border-bottom: 1px solid #efeff6;
  }

  .tox .tox-statusbar {
    background-color: rgba(239, 239, 246, 0.4);
    border-top: 0;
  }
}

.white-inputs {
  label {
    font-weight: normal;
  }

  input,
  textarea {
    background: #fff;
    border: 1px solid #efefef;
  }

  .form-check {
    label {
      color: #000;
    }
  }

  select {
    background-color: #fff;
    border: 1px solid #efefef;
  }
}

.kh-jumbotron {
  background-color: #f9fcfd;
  border: 1px solid #e4f3f8;
  @include border-radius(20px);
  padding: 25px 20px;

  @media (max-width: 575.9px) {
    padding: 25px 10px;
  }
}

.kh-header-tips {
  color: $green-dark;
  font-size: large;
  margin-bottom: 15px;
  margin-top: 30px;

  span {
    i {
      background-image: url(../images/svgs/ico-idea.svg);
      background-size: contain;
      background-repeat: no-repeat;
      display: inline-block;
      width: 15px;
      height: 15px;
      margin-right: 5px;
    }
  }
}

.kh-tips-list {
  list-style-type: none;

  li {
    margin-bottom: 10px;
    margin-left: 2px;

    span {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      i {
        background-image: url(../images/svgs/ico-ellipse.svg);
        background-size: contain;
        background-repeat: no-repeat;
        display: inline-block;
        width: 8px;
        height: 8px;
        margin-right: 10px;
        flex: 0 0 8px;
      }
    }
  }
}

.tag-container,
.tools-tag-container {
  padding: 10px;
  border-radius: 5px;
  display: block;

  .tag,
  .tool-tag {
    padding: 10px 15px;
    border-radius: 20px;
    margin: 5px;
    background-color: #f2f2f2;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    color: #1d4354;

    i {
      margin-left: 5px;
      cursor: pointer;

      &.ico-true {
        background: url(../images/svgs/ico-true.svg) no-repeat;
        width: 12px;
        height: 10px;
        display: inline-block;
        background-size: 10px;
        background-position: center;
        margin-left: 0;
        margin-right: 5px;
      }
    }
  }
}

.form-postjob {
  .form-group {
    margin-bottom: 20px;

    label {
      font-weight: bold !important;
    }

    .hint {
      font-size: 0.8em;
      color: #9c9ca8;
      margin-bottom: 1rem;
    }
  }
  .examples {
    margin-top: 10px;
  }
}
.ico-post-project {
  svg {
    transform: scale(0.8);
  }
}
.pay-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 575.9px) {
    flex-direction: column;
  }
  li {
    flex: 0 0 49%;
    padding: 1rem;
    background: #fff;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    @media (max-width: 575.9px) {
      flex: 0 0 100%;
      margin-bottom: 10px;
    }

    cursor: pointer;
    transition: box-shadow 1s, border.5s;
    .icon-item {
      flex: 0 0 25px;
      margin-right: 15px;
    }
    .title {
      color: #000000;
      font-weight: bold;
      margin-bottom: 10px;
    }
    p {
      max-height: 65px;
      color: #9c9ca8;
      overflow: hidden;
    }
    &.selected {
      border: 3px solid rgba(30, 137, 145, 0.47);
      box-shadow: 0px 3px 6px rgba(30, 137, 145, 0.39);
    }
  }
}
