.h-cover {
  max-height: 550px;
  overflow: hidden;
}

.cover__profile {
  position: relative;

  img {
    width: 100%;
  }

  .overlay-cover {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
  }

  &:after {
    position: absolute;

    content: "";
    bottom: -1px;
    right: 0;
    width: 100.5%;
    height: 245px;

    background: url(../images/svgs/bg-profile.svg) no-repeat bottom center;
    background-size: 100%;

    @media (min-width: 992px) {
      //  height: 183px;
      bottom: -1px;
    }

    @media (min-width: 1200px) {
      //height: 22.5%;
    }
  }

  .breadcrumb__bar {
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    z-index: 10;

    @media (max-width: 575.9px) {
      top: 75px;
    }

    .breadcrumb-item {
      color: $white;

      a {
        color: inherit;
        font-weight: bold;
      }

      &.active {
        a {
          font-weight: normal;
        }
      }
    }
  }
}

.profile-img {
  position: relative;
  overflow: visible !important;

  .ico-upload {
    width: 30px;
    height: 30px;
    right: -15px;
    position: absolute;

    top: 20px;
    z-index: 10;

    width: 30px;
    height: 30px;
    @include border-radius(50%);
    background: url(../images/pngs/ico-cam.png) no-repeat;
    background-size: contain;
    display: inline-block;
    text-indent: 10000px;
  }
}

input {
  &.ico-upload {
    cursor: pointer;
  }
}

.profile-edit {
  .action-btns {
    margin-top: 50px;

    @media (max-width: 576px) {
      text-align: center !important;
    }

    .btn {
      @include border-radius(20px);
      height: 40px;
      width: 120px;
      margin-right: 20px;

      @media (max-width: 576px) {
        margin-bottom: 10px;
      }

      &:last-child {
        margin-right: 0;
      }

      &.btn-w-icon {
        box-shadow: 0px 5px 25px 0px rgba(31, 135, 141, 0.66);
      }
    }

    .btn-delay {
      width: 180px;
    }

    .btn-gray {
      background: #ededed;
    }
  }
}

.upload__btn {
  position: absolute;
  right: 0;
  bottom: 0px;
  z-index: 100;
  width: 100%;
  text-align: right;

  @media (min-width: 576px) {
    bottom: 105px;
  }

  @media (min-width: 992px) {
    height: 18%;
    bottom: 155px;
  }

  .btn-upload {
    width: 170px;
    height: 45px;
    border-radius: 20px;
    font-weight: bold;
    line-height: 35px;

    @media (max-width: 575.9px) {
      height: 35px;
      width: 140px;
      line-height: 25px;
    }
  }
}

.info__profile {
  @extend .item__three__diff;
  box-shadow: none;
  background: transparent;
  @include border-radius(0);

  .profile-img {
    top: -50px;
    left: calc(50% - 50px);
  }

  .data-person {
    margin: 0;
    text-align: center;

    span {
      justify-content: center;
      margin-bottom: 5px;
    }
  }

  .btn-contact {
    @include border-radius(20px);

    height: 42px;
    font-weight: bold;
    position: relative;
    box-shadow: 0px 3px 6px 0px rgba(31, 135, 141, 0.46);
  }

  .statstic {
    margin-bottom: 10px;

    span {
      font-weight: bold;
      color: $black-light;
      font-size: 1.1em;
      margin: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.h-border-bottom {
  border-bottom: 1px solid rgba(112, 112, 112, 0.28);
  padding: 20px 0;

  &:first-child {
    padding-top: 50px;
  }

  &:last-child {
    border: 0;
  }
}

.aside-profile {
  padding: 0 20px;
  border: 1px solid #e4f3f8;
  background: #f9fcfd;
  @include border-radius(20px);
  margin-top: -95px;

  @media (max-width: 991.9px) {
    padding: 0 5px;
  }

  @media (max-width: 767.9px) {
    margin-bottom: 30px;
  }

  @media (max-width: 575.9px) {
    margin-top: 20px;
  }

  .btn-follow {
    position: absolute;
    right: 0;
    top: 10px;

    &:hover {
      color: #fff !important;
    }
  }

  .t-part {
    color: $green-dark;
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: 1em;
  }

  p {
    font-weight: bold;
    color: $black-light;
  }

  .list-skills {
    li {
      border-radius: 20px;
      margin: 7px 3px;
      background-color: #f2f2f2;
      padding: 6px 24px;
      color: #1d4354;
      display: inline-block;
      font-weight: bold;
    }
  }

  .lang {
    color: #000;
    font-weight: bold;

    li {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .accounts {
    li {
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      i {
        width: 25px;
        height: 25px;
        display: flex;
        margin-right: 15px;
      }

      a {
        color: $black-light;
        text-decoration: underline;
        font-weight: bold;
        font-size: 1.1em;
      }
    }

    .ico-behance {
      background: url(../images/svgs/ico-behance.svg) no-repeat center;
      background-size: contain;
    }

    .ico-linkedin {
      background: url(../images/svgs/ico-linkedin.svg) no-repeat center;
      background-size: contain;
    }
  }
}

.Tabs__details {
  margin-bottom: 50px;

  .nav-tabs {
    border: 0;
    margin-bottom: 15px;
  }

  .nav-link {
    color: $black-light;

    font-size: 1.2em;
    position: relative;
    padding-left: 0;
    padding-right: 0;
    margin: 0 1rem;
    border: 0;
    border-radius: 0;

    &:after {
      content: "";
      position: absolute;
      bottom: 2px;
      left: 0;
      width: 100%;
      height: 1px;
      background: transparent;
    }

    &.active {
      color: $black;
      border: 0;
      background: transparent;

      &:after {
        background: url(../images/svgs/ico-border.svg) no-repeat;
        background-size: contain;
      }
    }

    &:hover {
      border: 0;
    }
  }

  .tab-content {
    border: 1px solid #e4f3f8;
    background: #f9fcfd;
    @include border-radius(20px);
    padding: 20px;
  }
}

.project-item {
  width: 48%;
  margin-right: 3%;
  // overflow: hidden;
  padding: 10px 15px;
  border-radius: 15px;
  background: #fff;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  padding-bottom: 15px;
  display: inline-block;
  margin-bottom: 20px;
  position: relative;

  @media (max-width: 575.9px) {
    width: 100%;
    margin-right: 0;
  }
  .see-project {
    position: absolute;
    top: 0;
    left: 0;
    .btn {
      border-radius: 20px;
      background: #fff;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
      width: 70px;
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 0;
      &:after {
        display: none;
      }
      &:focus {
        outline: 0;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
      }
      &:active {
        border: 0;
        background: transparent;
        outline: 0;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
      }
    }
    .dropdown-menu {
      font-size: 1em;
      box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.16);
      border: 0;
      a {
        &:hover {
          background: $green-extra-light;
        }
      }
    }
  }

  &:nth-child(2n) {
    margin-right: 0;
  }

  .img-project {
    padding-bottom: 15px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgba(118, 118, 118, 0.28);
    text-align: center;
    height: 165px;
    overflow: hidden;
  }

  .date {
    color: $green-dark;
  }

  .ico-fav {
    width: 35px;
    height: 35px;
    background: url(../images/svgs/ico-fav.svg) no-repeat center top 5px;
    background-size: contain;
    display: inline-block;
    vertical-align: middle;
  }
}

.ico-clock {
  width: 20px;
  height: 20px;
  background: url(../images/svgs/ico-clock.svg) no-repeat center;
  background-size: contain;
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
}

.lSAction-custom {
  a {
    &:first-child {
      margin-right: 25px;
    }

    svg {
      width: 30px;
      transition: all 1s;

      path {
        fill: #1e201d;
        opacity: 0.26;
      }
    }

    &:hover,
    &.active {
      svg {
        transform: scale(1.5);

        path {
          fill: $green-dark;
          opacity: 1;
        }
      }
    }
  }
}

.list-reviews {
  .item-review {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid rgba(112, 112, 112, 0.28);

    &:last-child {
      border-bottom: 0;
      padding: 0;
      margin: 0;
    }

    .rate {
      position: relative;

      &:after {
        content: "";
        position: absolute;
        bottom: -7px;
        left: 0;
        height: 1px;
        width: 40%;
        background: #a4a4a4;
      }
    }

    p {
      color: #000;
      font-weight: bold;

      margin-bottom: 0;
    }
  }
}

#more {
  display: none;
}

/*********************************************************Profile Data ****************************/

.Tabs__steps {
  .nav-tabs {
    border: 0;
    margin-bottom: 50px;
  }

  .nav-item {
    width: 20%;
    text-align: center;
    position: relative;

    &:after {
      position: absolute;
      content: "";
      top: 26px;
      height: 1px;
      width: 100%;
      left: 50%;
      background: #d1d3d4;
      z-index: -1;

      @media (max-width: 575.9px) {
        top: 21px;
      }
    }

    &:last-child {
      &:after {
        display: none;
      }
    }

    .nav-link {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      background: transparent !important;
      border: 0;
      padding: 0;

      span {
        @media (max-width: 575.9px) {
          display: none;
        }
      }

      i {
        width: 50px;
        height: 50px;
        @include border-radius(50%);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 8px;
        position: relative;
        background: #d1d3d4;

        @media (max-width: 575.9px) {
          width: 40px;
          height: 40px;
        }

        &:after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          @include border-radius(50%);
          background: $main-gradient;

          z-index: 0;
          opacity: 0;
        }

        svg {
          width: 20px;
          z-index: 1;
        }
      }
    }

    &.active {
      &:after {
        background: linear-gradient(to right, #23757b 0%, #d1d3d4 99.96%);
      }

      .nav-link {
        i {
          &:after {
            opacity: 1;
            transform: scale(1.1);

            transition: all 2s;
          }

          svg {
            path {
              fill: #fff;
            }
          }
        }
      }
    }
  }
}

.docs-datepicker-trigger {
  width: 70px;

  .ico-calendar {
    width: 15px;
    height: 20px;
    background: url(../images/svgs/ico-ca-white.svg) no-repeat center;
    display: inline-block;
    background-size: contain;
  }
}

.secondary__section {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: -80px;
    left: 65%;
    height: 75px;
    width: 75px;
    background: url(../images/pngs/ico-bg.png) no-repeat;
    background-size: 60%;
  }

  .img_name_user {
    position: absolute;
    left: 20px;
    top: -60px;

    @media (max-width: 575.9px) {
      position: relative;
      top: 0;
      text-align: center;
      margin-bottom: 15px;
      left: auto;
    }

    .profile-img {
      border-color: $green-dark;

      @media (max-width: 575.9px) {
        margin: 0 auto;
      }
    }
  }

  .welcome-txt {
    padding-left: 100px;

    @media (max-width: 575.9px) {
      padding-left: 0px;
    }
  }
}

.nav-tabs-horz {
  .nav-item {
    width: 100%;
    margin-bottom: 30px;

    &:after {
      display: none;
    }

    .nav-link {
      span {
        @media (max-width: 575.9px) {
          display: block;
        }
      }
    }
  }

  .ico-add {
    background: url(../images/pngs/ico-add-tabs.png) no-repeat center;
    width: 40px;
    height: 40px;
    background-size: contain;
  }

  .ico-delete {
    background: url(../images/pngs/ico-remove.png) no-repeat center;
    width: 12px;
    height: 12px;
    background-size: contain;
    display: inline-block;
  }

  .ico-edit {
    background: url(../images/pngs/ico-edit.png) no-repeat center;
    width: 10px;
    height: 10px;
    background-size: contain;
    display: inline-block;
    margin-right: 5px;
  }
}

.txt-nowrap {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.chosen-container-single .chosen-single {
  @extend .form-control;
  border: 1px solid #efefef;
  background: #fff;
  box-shadow: none;

  span {
    line-height: 35px;
  }
}

/********************Profile update****************************/
.tab-content {
  &.custom {
    border: 0;
    background: transparent;

    .list-extend {
      li {
        padding: 0 0 0 10px;
        margin-bottom: 15px;

        .btn {
          height: 38px;
          width: 66px;
          border-radius: 20px;
          visibility: hidden;
        }
      }
    }
  }
}

.b-border {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 60px;
    height: 3px;
    background: url(../images/svgs/ico-border.svg) no-repeat;
    background-size: 100% 100%;
  }
}
.btn {
  &.btn-edit,
  &.btn-add,
  &.btn-remove {
    padding: 0;
  }
  .ico-edit {
    width: 2em;
    height: 1em;
    background-image: url(../images/svgs/ico-edit-green.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50%;
    cursor: pointer;
    display: block;
  }
  .ico-add {
    background: url(../images/pngs/ico-add-tabs.png) no-repeat center;
    width: 35px;
    height: 35px;
    background-size: contain;
    display: inline-block;
  }
  .ico-delete {
    background: url(../images/pngs/ico-remove.png) no-repeat center;
    width: 15px;
    height: 15px;
    background-size: contain;
    display: inline-block;
  }
}
.modal-backdrop.show {
  opacity: 0.8;
}
.modal-projects {
  .modal-content {
    background: transparent;
    color: #fff;
    .modal-header {
      a {
        text-decoration: underline;
        color: #fff;
      }
    }
    .close {
      color: #fff;
      text-shadow: none;
      opacity: 1;
      font-weight: normal;
    }
    .profile-img {
      @media (max-width: 575.9px) {
        width: 60px;
        height: 60px;
        flex: 0 0 60px;
      }
    }
  }

  .list-images {
    background: #fff;

    .img-project {
      margin-bottom: 30px;
      text-align: center;
      img {
        max-width: 100%;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .apperciate {
    background: #fff;
    padding: 2rem;
    text-align: center;
    .btn {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background: linear-gradient(#1f8a91 0%, #44a394 53.27%, #62b796 100%);
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
      svg {
        animation: heartbeat 2s infinite;
      }
    }
  }
}
@keyframes heartbeat {
  0% {
    transform: scale(0.75);
  }
  20% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.75);
  }
  60% {
    transform: scale(1);
  }
  80% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0.75);
  }
}
.projects__list {
  li {
    padding: 10px 15px;
    border-radius: 15px;
    background: #fff;
    margin-bottom: 20px;
    p {
      color: #404143;
      opacity: 0.4;
      font-family: "Raleway-Medium";
    }
    a {
      color: #7da3ef;
    }
    .bg-date {
      padding-left: 30px;
      background: url(../images/bg-date.svg) no-repeat left -10px top -8px;
    }
    .date {
      display: flex;
      align-items: center;
      span {
        font-size: 18px;
        i {
          margin-right: 10px;
        }
      }
      .calendar {
        background: url(../images/ico-calendar.svg) no-repeat center;
        font-size: 8px;
        font-weight: bold;
        width: 16px;
        height: 17px;
        display: inline-block;
        text-align: center;
        line-height: 20px;
        margin-right: 10px;
      }
    }
    .attach {
      padding-top: 20px;
      margin-top: 20px;
      border-top: 1px solid rgba(112, 112, 112, 0.28);
      a {
        margin-right: 20px;
        text-decoration: underline;
      }
    }
  }
}
