
.body-content{
    position: relative;
    top: 100px;

    section{
        
        &.first-section{
            
            @media (max-width: 575.9px) {
                margin: 0 auto;
                
            }

            img{
                max-width: 80%;
                max-height: 80%;

                @media (max-width: 767.9px){
                    max-width: 70%;
                    max-height: 70%;
                    margin: 0 auto;
                }

                @media (max-width: 575.9px) {
                    margin: 0 auto;
                    max-width: 60%;
                    max-height: 60%;
                }
            }

            .bg-slider{
                position: absolute;
                z-index: -9999;
                width: 1080px;
                height: 680px;
                left: -80px;
                top: -120px;
                img{
                    width: 120%;
                    height: 100%;
                }
            }

        }

        &.second-section {
            
            img{
                width: 100%;
                height: 330px;

                @media (max-width: 767.9px){
                    width: 85%;
                    height: 200px;
                }

                @media (max-width: 575.9px) {
                    width: 70%;
                    height: 130px;
                    margin-bottom: 10px;
                }
            }
        }

        &.forth-section {
            
                .border-bottom-green{
                    border-bottom: 2px solid #1F878D;
                }
    
                .ourstory-img-wrapper {
                    background-image: url(../images/pngs/inner-our-story.png);
                    background-size: 90%;
                    background-repeat: no-repeat;
                    display: inline-block;
                    height: 550px;
                    width: 830px;
                    padding: 180px 250px 50px 100px;

                    @media (max-width: 767.9px){
                        background-size: 100%;
                        height: 370px;
                        width: 520px;
                        padding: 90px 0 0 0;
        
                    }

                    @media (max-width: 575.9px){
                        background-size: 110%;
                        height: 250px;
                        width: 320px;
                        padding: 60px 0 0 0;
                        
                    }

                    h5{
                        @media (max-width: 575.9px){
                            font-size: 1em;
                        }
                    }
                   
                }

                .our-story-content{
                    width: 400px;
                    max-height: 68%;
                    height: 68%;
                    margin: 0 auto;
                    overflow: hidden;

                    @media (max-width: 767.9px){
                        width: 300px;
                        height: 180px;
                        line-height: 1.8em;
                        font-size: 0.75em;
                    }

                    @media (max-width: 575.9px){
                        width: 170px;
                        height: 150px;
                    }
                   
                }
    
                .right-left-arrow-container{

                    a{
                        background-size: contain;
                        background-repeat: no-repeat;
                        width: 50px;
                        height: 25px;

                        @media (max-width: 575.9px){
                            width: 35px;
                            height: 17px;
                        }
                    
    
                        &.left-arrow-icon{
                            background-image: url(../images/svgs/ico-prev.svg);
                        }
    
                        &.right-arrow-icon{
                            background-image: url(../images/svgs/ico-next.svg);
                        }
                    }
    
                }
            }
        }
        
        
        h1.kh-heading{
            font-size: 2.5em;
            
            @media (max-width: 767.9px){
                margin: 0 auto;
                font-size: 1.8em;
                text-align: center;
            }

            @media (max-width: 575.9px) {
                margin: 0 auto;
                font-size: 1.2em;
                text-align: center;
            }
            
            .sub-heading{
                color: #1F878D;
                font-weight: bold;
                display: block;

                span{
                    color: #00BCD4
                }
            }
        }

        .description{
            line-height: 2.2em;

            &.first{
                width: 75%;
            }

            @media (max-width: 767.9px){
                margin: 0 auto;
                width: 90%;
                text-align: center;
            }

            @media (max-width: 575.9px) {
                margin: 0 auto;
                width: 90%;
                text-align: center;
                
            }

            &.journey{
                
                @media (max-width: 575.9px) {
                    margin: 0 auto;
                    width: 90%;
                    text-align: center;
    
                }
            }

            &.mission{
                margin-top: 20px;
                width: 75%;

    
                @media (max-width: 575.9px) {
                    width: 90%;
                    text-align: center;
                    margin: 0 auto 10px auto;
                }
            }

            &.vision{
                margin-top: 20px;
                line-height: 2.2em;
                
                @media (max-width: 575.9px) {
                    width: 90%;
                    text-align: center;
                    margin: 0 auto;
                }
            }

            &.story{
                text-align: center;
                margin-bottom: 100px;

                @media (max-width: 575.9px){
                    line-height: 1.7em;  
                }
        }
        
    }

    .mv-heading{

        span {
            font-size: 1.3em;
        }
    }

    .ab-mission-vision-heading{    
        background-size: 40px;
        background-repeat: no-repeat;
        display: inline-block;
        width: 80px;
        height: 80px;
        margin-right: 10px;
        background-position: center;
        border-radius: 13px;


        @media (max-width: 575.9px) {
            background-size: 20px;
            width: 40px;
            height: 40px;
            margin-right: 5px;
        }
    }

    .mission-icon{
        background-image: url(../images/svgs/ico-mission.svg);
        background-color: #1E8991;
    }

    .vision-icon{
        background-image: url(../images/svgs/ico-vision.svg);
        background-color: #00BCD4;
    }

    .mission-vision-bg{
        background-image: url(../images/svgs/mission-vision-bg.svg);
        background-size: 50px;
        background-repeat: no-repeat;
        display: inline-block;
        width: 80px;
        height: 130px;
        background-position: center;
        position: relative;
        left: -60px;
        z-index: -9999;

        @media (max-width: 575.9px) {
            background-size: 25px;
            width: 60px;
            height: 65px;
            left: -40px;
        }
    }

    .shift-left{
        position: relative;
        left: -60px;
    }
}
