footer {
    background: $green-dark url(../images/svgs/bg-footer.svg)no-repeat bottom right;
    background-size: cover;
    padding: 10px 0;
    color: #fff;
    padding-top: 70px;
    font-size: .9em;
    color: rgba(255, 255, 255, .8);

    font-family: 'Raleway-Medium';

    .title__footer {
        margin-bottom: 15px;
        font-family: 'Raleway-Bold';
        font-size: 1.3em;
        color: #fff;
    }


    .info__footer {
        span {
            display: flex;
            margin-bottom: 15px;
            width: 60%;
            align-items: center;

            &:first-child {
                align-items: flex-start;
            }


        }

        i {
            width: 23px;
            height: 20px;
            margin-right: 12px;



            $icons: ico-location ico-phone ico-mail;

            @each $icon in $icons {
                &.#{$icon} {
                    background-image: url(../images/svgs/#{$icon}.svg);
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;

                }
            }

            &.ico-phone,
            &.ico-mail {
                width: 15px;
            }
        }



    }


    .follow__title {
        font-family: 'Raleway-Bold';
        font-size: 1.1em;
        margin: 30px 0 15px;
        color: #fff;

    }

    .social__links {
        margin-top: 0;

        ul {
            margin-right: 0;
            float: left;
        }

        i {
            margin: 0 12px;

            &:first-child {
                margin-left: 0;
            }

            $icons: ico-fb-white ico-tw-white ico-insta-white ico-google-white ico-youtube-white ico-linked-white;

            @each $icon in $icons {
                &.#{$icon} {
                    background-image: url(../images/svgs/#{$icon}.svg);
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;

                }
            }


            &.ico-linked-white {
                width: 15px;

            }
        }




    }

    .links {

        li {
            padding: 5px 0;
        }

        a {
            color: inherit;

        }
    }

}

.col-footer {
    @media (max-width: 1200px) {
        padding: 0 15px;



    }

    &:first-child {
        width: 35%;

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {

            width: 100% !important;
            padding: 0 10px;


        }

    }

    &:nth-child(2),
    &:nth-child(3) {
        width: 20%;

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {

            padding: 0 10px;


        }

    }

    &:nth-child(4) {
        width: 25%;

        @media (max-width: 1200px) {
            padding: 0;



        }

        @media (max-width: 991.9px) {

            width: 50% !important;
            margin-bottom: 20px;


        }

        @media (max-width: 575.9px) {
            padding: 0 10px;

            width: 100% !important;


        }
    }



}

.form-disc {

    padding: 20px 0;

    input {
        height: 40px;
        flex: 0 0 220px !important;
        padding-right: 80px;
        font-family: 'Raleway-Regular';
        font-size: 1em;
        border: 0;
        border-radius: 25px !important;
        background: #fff;

        box-shadow: 0px 13px 99px rgba(13, 30, 82, 0.1);

        &::placeholder {
            color: $black-light;

        }
    }

    button,
    input[type=button] {
        height: 40px;
        width: 80px !important;
        border-radius: 20px !important;
        background: #58AA82;
        color: #ffff;
        margin-left: -75px;
        font-family: 'Raleway-Bold';
        font-size: 1em;
        border: 0 !important;
    }

}

.copy__right {
    margin-top: 45px;
    padding: 20px 0;
    border-top: 1px solid rgba(255, 255, 255, .31);

    .sy-copy {
        font-size: 1.5em;
        margin-right: 5px;
        font-family: 'Raleway-Light';

    }
}