
.company__profile__section{

    .cp__left__part{
        .ico-edit-green{
            width: 2em;
            height: 1em;
            background-image: url(../images/svgs/ico-edit-green.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 50%;
            cursor: pointer;
        }

        .company__info__list{
            width: 95%;
            margin: auto;
            font-size: 0.85rem;
            
            li{
                .start__ico__green{
                    width: 1.25em;
                    height: 1.25em;
                    margin-right: 0.4em;
                    background-repeat: no-repeat;
                    background-position: cover;
                    background-size:contain;

                    &.ico__website{
                        background-image: url(../images/svgs/ico-web-green.svg);
                    }

                    &.ico__headquarters{
                        background-image: url(../images/svgs/ico-pin.svg);
                    }

                    &.ico__founded__in{
                        background-image: url(../images/svgs/ico-watch-green.svg);
                    }
                }
            }
        }

        .last__line{
           

        }
    }
    
}