.right-part-signup-2 {

    background: $white;

    .bg__sign-in-up__small {
        background: url(../images/pngs/bg-icons-new.png) no-repeat center bottom;
        background-size: 50%;

        @media (max-width: 991.9px) {
            background-size: 40%;
        }

        @media (max-width: 575.9px) {
            background-size: 80%;

        }
    }

    @media (max-width: 991.9px) {
        padding-top: 80px;
    }

    .line__height__18 {
        line-height: 1.8rem;
    }

    .font__125 {
        font-size: 1.25rem
    }
}

.height__100vh {
    height: 100vh;

    @media (max-width: 991.9px) {
        margin-top: -100px;
    }

    @media (max-width: 575.9px) {
        margin-top: -120px;
    }
}