.banner__slider {
  position: relative;
  height: 100vh;
  overflow: hidden;

  .right-part {
    width: 75%;
    background: url(../images/pngs/bg-slider.png) no-repeat top 15px right -78px;
    background-size: 810px;
    position: absolute;
    height: 100vh;
    top: 0;
    right: 0;

    @media (max-width: 1199.9px) {
      width: 85%;
      background-size: 750px;
    }

    @media (max-width: 991.9px) {
      width: 100%;
      background-size: 730px;
    }

    @media (max-width: 767.9px) {
      background-size: 550px;
    }

    .img-slide {
      position: relative;
      width: 435px;
      height: 435px;
      margin-right: 10%;
      float: right;
      margin-top: 105px;

      @media (max-width: 1199.9px) {
        width: 400px;
        margin-right: 11%;
      }

      @media (max-width: 991.9px) {
        margin-right: 7%;
        width: 405px;
      }

      @media (max-width: 767.9px) {
        width: 270px;
        height: 275px;
      }

      &:before {
        content: "";
        position: absolute;
        bottom: 25%;
        left: -2px;
        height: 90px;
        width: 90px;
        background: url(../images/pngs/ico-bg.png) no-repeat;
        background-size: 60%;
      }
    }

    .btn-slick {
      background-size: 100%;
      width: 30px;
      height: 30px;
      z-index: 100;
      position: absolute;
      cursor: pointer;

      &:focus {
        outline: 0;
        border: 0;
      }

      &.slick-arrow-prev {
        background-image: url(../images/svgs/ico-prev.svg);
        background-repeat: no-repeat;
        left: -35px;
        bottom: 22%;
      }

      &.slick-arrow-next {
        background-image: url(../images/svgs/ico-next.svg);
        background-repeat: no-repeat;
        left: 10px;
        bottom: 20%;
      }
    }
  }

  .content-slide {
    margin-top: 100px;

    @media (max-width: 767.9px) {
      margin-top: 50vh;
    }

    h1 {
      @media (max-width: 767.9px) {
        font-size: 2em;
      }
    }
  }

  .heading {
    font-family: "Raleway-Light";
    animation-delay: 300ms;
    margin-bottom: 0.7em;

    .green-light {
      color: $green-light;
      font-family: "Raleway-Bold";
    }

    .green-dark {
      color: $green-dark;
      font-family: "Raleway-Bold";
    }
  }

  .description {
    color: $black-light;
    font-size: 1.2em;
    animation-delay: 1000ms;
    margin-bottom: 1em;
    width: 65%;

    @media (max-width: 767.9px) {
      width: 100%;
      font-size: 1em;
    }
  }

  .actions-slide {
    animation-delay: 1400ms;

    a {
      &:first-child {
        margin-right: 15px;
        box-shadow: 0px 5px 25px 0px rgba(31, 135, 141, 0.66);

        @media (max-width: 991.9px) {
          margin-bottom: 15px;
        }
      }

      @media (max-width: 767.9px) {
        padding: 0.8em 0.5em;
        width: 165px;
      }

      @media (max-width: 575.9px) {
        padding: 0.8em 0.5em;
        width: 150px;
        font-size: 0.9em;
      }
    }
  }

  .animate {
    -moz-opacity: 0;
    -khtml-opacity: 0;
    -webkit-opacity: 0;
    opacity: 0;
    filter: alpha(opacity=0 * 100);
  }
}

.home__banner {
  .w-line {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 13px;
      left: 0;
      background: rgba(112, 112, 112, 0.28);
      height: 1px;
      width: 82%;
      -webkit-clip-path: inset(0 0 0 90%);
      clip-path: inset(0 0 0 90%);
      opacity: 0;
      -webkit-transition: all 9s;
      transition: all 9s;

      @media (max-width: 1199.9px) {
        width: 65%;
      }

      @media (max-width: 767.9px) {
        width: 50%;
      }

      @media (max-width: 575.9px) {
        display: none;
      }
    }

    &.animated {
      &:before {
        opacity: 1;
        clip-path: inset(0 0 0 0);
      }
    }
  }
}

.social__links {
  margin-top: -50px;
  margin-bottom: 50px;

  ul {
    float: right;
    margin-right: -5%;

    @media (max-width: 1200px) {
      margin-right: 0;
    }

    @media (max-width: 575.9px) {
      margin: 0 auto;
    }

    li {
      display: inline-block;

      a {
        display: block;
      }
    }
  }

  i {
    width: 20px;
    height: 20px;
    display: inline-block;
    margin: 0 8px;
    $icons: ico-fb ico-tw ico-insta ico-google ico-youtube ico-linked;

    @each $icon in $icons {
      &.#{$icon} {
        background-image: url(../images/svgs/#{$icon}.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
      }
    }

    &.ico-linked {
      width: 15px;
    }
  }
}

