//--==============================Start Productivity Section ==============================
.Productivity__section {
  .heading__section {
    h2 {
      width: 130%;
      margin-left: -21%;
      margin-top: 15%;
      margin-bottom: 0.8em;

      @media (max-width: 767.9px) {
        margin-left: 0;
        width: 100%;
        margin-top: 10px;
      }
    }
  }

  p {
    font-family: "Raleway-ExtraLight";
    line-height: 2.2em;
  }

  .ico-arr-view {
    width: 16px;
    height: 10px;
    display: inline-block;
    margin-left: 5px;
    background: url(../images/svgs/ico-arr-view.svg) no-repeat center;
    background-size: 100%;
    background-color: transparent;

    animation: bounceRight 1s infinite;
  }

  .description__section {
    @media (max-width: 767.9px) {
      text-align: center;
    }
  }
}

/* right bounce */

@keyframes bounceRight {
  0% {
    -webkit-transform: translateX(5px);
    transform: translateX(5px);
  }

  40% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  100% {
    -webkit-transform: translateX(5px);
    transform: translateX(5px);
  }
}

/* /right bounce */

