//--==============================Start Freelancing Section ==============================
.freelancing__section {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: -130px;
    left: 0;
    width: 500px;
    height: 500px;
    background: url(../images/pngs/bg-sm.png) no-repeat;
    z-index: -1;
    background-size: contain;
  }

  &:after {
    content: "";
    position: absolute;
    top: 43%;
    right: 0;
    width: 1000px;
    height: 1000px;
    background: url(../images/svgs/bg-lg.svg) no-repeat;
    z-index: -1;
    background-size: 90%;
    margin-right: -20%;
  }

  p {
    @media (max-width: 767.9px) {
      text-align: center;
    }
  }
}

.col-activity {
  display: inline-block;
  margin-right: 30px;

  @media (max-width: 575.9px) {
    margin: 0 auto;
  }

  &:last-child {
    margin-right: 0;
  }

  &:nth-child(2) {
    margin-top: 54px;

    @media (max-width: 767.9px) {
      margin-top: 0;
      margin-right: 0;
    }

    @media (max-width: 575.9px) {
      margin: 0 auto;
    }
  }

  &:nth-child(3) {
    margin-top: 185px;

    @media (max-width: 991.9px) and (min-width: 768px) {
      margin-top: 0;
    }

    @media (max-width: 767.9px) {
      margin: 0 auto;
    }
  }
}

.activity {
  width: 210px;
  height: 185px;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0px 6px 20px rgba(98, 183, 150, 0.5);
  margin-bottom: 30px;
  padding: 10px;

  @media (max-width: 1199.9px) and (min-width: 992px) {
    width: 170px;
    height: 170px;
  }

  @media (max-width: 991.9px) and (min-width: 768px) {
    width: 155px;
    height: 140px;
  }

  .img-activity {
    margin-bottom: 20px;
    text-align: center;

    img {
      width: 70%;
    }
  }

  .txt-activity {
    font-family: "Raleway-Bold";
    color: #000;
    font-size: 1.2em;
    text-align: center;
    transition: color 1s;
  }

  &:hover {
    .txt-activity {
      color: $green-dark;
    }
  }
}

.more-details {
  @media (max-width: 767.9px) {
    text-align: center;
  }

  a {
    width: 100px;
    font-family: "Raleway-Medium";
  }
}

//--==============================End Freelancing Section ==============================
//--==============================Start How it works Section ==============================
.how__works__section {
  .heading__section {
    margin-bottom: 80px;

    @media (max-width: 767.9px) {
      margin-bottom: 2em;
    }
  }

  .it__em {
    .item__works {
      height: 370px;
      background: #59d286;
      color: $white;
      text-align: center;
      @include border-radius(20px);
      position: relative;
      overflow: hidden;

      @media (max-width: 767.9px) {
        width: 400px;
        margin: 0 auto 20px;
      }

      @media (max-width: 575.9px) {
        width: 280px;
      }

      &:before {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        width: 120px;
        height: 120px;
        background: rgba(156, 238, 183, 0.42);
        border-radius: 50%;
        margin-right: -30px;
        margin-top: -30px;
      }

      h6 {
        font-family: "Raleway-Bold";
        margin-bottom: 20px;
      }

      p {
        width: 55%;
        line-height: 1.7em;
        margin: 0 auto;
      }
    }

    &:nth-child(2) {
      .item__works {
        background: linear-gradient(#1f8a91 0%, #44a394 53.27%, #62b796 100%);

        &:before {
          background: rgba(145, 183, 233, 0.18);
        }
      }
    }

    &:nth-child(3) {
      .item__works {
        background: #00bcd4;

        &:before {
          background: rgba(103, 194, 204, 0.67);
        }
      }
    }
  }
}

