.message-client {
  position: relative;

  .img-client {
    position: absolute;
    width: 100px;
    height: 100px;
    top: 15px;
    left: -50px;
    z-index: 2;

    @media (max-width: 575.9px) {
      position: relative;
      left: 50%;
      margin-left: -50px;
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}

.txt-client {
  padding: 15px 80px;
  overflow: hidden;
  background: #fff;
  width: 550px;
  height: 130px;
  @include border-radius(95px);
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.06);
  -webkit-clip-path: inset(0 90% 0 0);
  clip-path: inset(0 90% 0 0);
  opacity: 0;
  -webkit-transition: all 2s;
  transition: all 2s;

  @media (max-width: 767.9px) {
    width: 450px;
    height: 150px;
  }

  &.animated {
    opacity: 1;
    clip-path: inset(0 0 0 0);
  }

  .title {
    font-family: "Raleway-Bold";
    margin-bottom: 10px;
  }

  .txt-message {
    color: #858585;
    line-height: 1.5em;
    font-size: 0.8em;
    position: relative;
  }

  .creator {
    float: right;
    text-align: center;
    color: #000;
    font-size: 0.9em;

    .name {
      font-family: "Raleway-Bold";
    }
  }
}

