.newsfeed {
    .tab-content {
        padding: 40px 0 0;
        background: transparent;
        border: 0;
    }
}

.newsfeed-wrapper {
    .cover__page {
        background-image: url(../images/pngs/bg-news.png);

        .person__cover {
            width: 100%;
            background: url(../images/pngs/cover-newsfeed.png) no-repeat right top 65px;
            background-size: 93%;
            margin: 0;
        }

    }

    .cover-left {

        background: url(../images/pngs/bg-left.png) no-repeat left bottom;
    }
}

.w-catg {
    max-width: 30%;
}