header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    transition: all .5s ease-in-out 0s;
    padding: 15px 0;

    .ico-notify {
        svg {
            width: 20px;

            path {
                fill: $green-light
            }
        }
    }

    .search-icon {
        width: 20px;
        height: 20px;
        display: inline-block;
        background: url(../images/pngs/ico-search.png)no-repeat center;
        background-size: contain;
    }

    .main__bar {
        display: flex;
        justify-content: space-between;
        width: 100%;
        align-items: center;

        @media (max-width:991.9px) {
            flex-wrap: wrap;
        }


        &:before {
            content: "";
            position: absolute;
            top: -15px;
            left: -43px;
            width: 380px;
            height: 170px;
            background: url(../images/svgs/bg-group.svg)no-repeat top left 20%;
            background-size: contain;
        }

        .logo {



            &.logo-v {
                display: block;

                height: 80px;
                transition: all .3s ease-out .3s;

                @media (max-width: 767.9px) {
                    height: 65px;

                }

                img {
                    max-height: 100%;
                    width: auto;
                }
            }

            &.logo-h {
                display: none;


                img {
                    max-width: 100%;
                }
            }


        }

        .menu-btn {
            display: none;
        }

        .menu-icon {
            cursor: pointer;
            float: right;
            padding: 20px;
            position: relative;
            user-select: none;

            @media (min-width:992px) {
                display: none;
            }

            .navicon {
                background: $green-dark;

                display: block;
                height: 3px;
                position: relative;
                transition: background .2s ease-out;
                width: 18px;
                @include border-radius(2px);

                &:before {
                    background: $green-dark;
                    ;
                    content: '';
                    display: block;
                    height: 100%;
                    position: absolute;
                    transition: all .2s ease-out;
                    width: 50%;
                    top: 7px;
                    right: 0;
                    @include border-radius(2px);
                }

                &:after {
                    background: $green-dark;
                    content: '';
                    display: block;
                    height: 100%;
                    position: absolute;
                    transition: all .2s ease-out;
                    width: 70%;
                    top: -7px;
                    right: 0;
                    @include border-radius(2px);
                }

                &.dark {
                    background: #707070;

                    &:before {
                        background: #707070;
                    }

                    &:after {
                        background: #707070;
                    }
                }
            }
        }

        .menu {

            @media (max-width:991.9px) {
                height: 0;

            }

            ul {
                clear: both;
                max-height: 0;
                overflow: hidden;
                transition: max-height .2s ease-out;
                margin: 10px 0;


                @media (min-width:992px) {
                    clear: none;
                    float: left;
                    max-height: none;
                    height: auto;
                    overflow: visible;
                    display: flex;
                    align-items: center;
                }

                @media (max-width:991.9px) {
                    background: $green-light;
                    padding: 0 20px;
                }

            }



            li {
                display: inline-block;
                color: $black-light;
                margin-right: 1.5em;
                position: relative;
                color: #767672;
                font-family: 'Raleway-Medium';

                @media (max-width:991.9px) {
                    width: 100%;
                    padding: 10px;
                    color: $white;
                    border-bottom: 1px solid rgba(255, 255, 255, .1);
                }

                .ico-arr-down {
                    background: url(../images/pngs/ico-arr-down.png)no-repeat center;
                    width: 10px;
                    height: 10px;
                    display: inline-block;
                    background-size: contain;
                    margin-left: 5px;

                    @media (max-width:991.9px) {
                        display: none;
                    }

                }

                &.dropdown-link {
                    &:after {
                        content: "";
                        width: 20px;
                        height: 3px;
                        background: $green-dark;
                        position: absolute;
                        bottom: -2px;
                        left: 0;
                        -webkit-clip-path: inset(0 80% 0 0);
                        clip-path: inset(0 80% 0 0);
                        opacity: 0;
                        -webkit-transition: all 2s;
                        transition: all 2s;

                        @media (max-width:991.9px) {
                            display: none;
                        }
                    }

                    &:hover {
                        color: $green-dark;

                        @media (max-width:991.9px) {
                            color: rgba(255, 255, 255, .7);

                        }

                        .ico-arr-down {
                            background: url(../images/pngs/ico-arr-down-green.png)no-repeat center;

                            background-size: contain;

                        }

                        &:after {
                            opacity: 1;
                            clip-path: inset(0 0 0 0);
                        }
                    }
                }



                a {
                    color: inherit;
                }




            }

            .btn {
                @include border-radius(20px);
                width: 90px;
                height: 40px;
                line-height: 35px;
                margin-right: 15px;

                @media (max-width: 1199.9px) {

                    width: 75px;
                    margin-right: 5px;


                }

                &.btn-white {
                    color: $green-dark;

                    &:hover {
                        color: $white;
                    }
                }

                &.btn-green {
                    color: $white;
                }

            }
        }
    }

    &.fixed__header {
        box-shadow: 0 1px 8px #b3b3b3;
        background: $white;
        padding: 10px 0;
        transition: all .5s ease-in-out 0s;

        .ico-notify {
            svg {


                path {
                    fill: $green-light
                }
            }
        }

        .main__bar {
            top: 0;

            .logo {

                &.logo-v {
                    display: none;


                }

                &.logo-h {
                    display: block;
                    width: 130px;
                    height: 50px;
                    padding: 5px 0;



                }
            }


        }

    }

}



header .menu-btn:checked~.menu ul {

    max-height: 80vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

header .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

header .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
    width: 100%;
}

header .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
    width: 100%;
}

header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
    top: 0;
}

header {
    &.h__home {
        li {
            color: #fff;

        }

    }
}

/*===================================DropDown=================================*/

.dropdown-link {
    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: -5px;
        width: 100%;
        height: 2px;
        left: 0;
        background: transparent;

        @media (max-width: 991.9px) {
            display: none;
        }
    }



    &:hover {
        &:after {
            background: $green-dark;

        }
    }

    .dropdown-content {
        position: absolute;
        min-width: 170px;
        left: 0px;
        opacity: 0;
        visibility: hidden;
        -moz-transition: opacity 0.2s 0.1s ease-out, visibility 0.1s 0.1s linear;
        -webkit-transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;
        -o-transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;
        transition: opacity 0.3s ease-out, visibility 0.1s 0.1s linear;

        @media (max-width: 991.9px) {
            position: relative;
            left: auto;
            opacity: 1;
            box-shadow: none;
            visibility: visible;

        }

        a {
            padding: 10px 5px;
            text-decoration: none;
            display: block;
            border-bottom: 1px solid red;
            background: #F0F0F0;
            display: block;
            margin-top: 20px;
            border: 1px solid rgba(235, 235, 235, .8);
            text-align: center;
            width: 100%;
            opacity: 0;
            position: relative;
            font-size: 13px;
            box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.05);
            z-index: 2;
            -moz-transition: opacity 0.1s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            -webkit-transition: opacity 0.1s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            -o-transition: opacity 0.15s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            transition: opacity 0.15s 0.15s ease-out, margin 0.5s 0.1s ease-out;
            margin-bottom: 5px;

            @media (max-width: 991.9px) {
                opacity: 1;
                border-bottom: 0;
                color: $white;
                margin: 0;
                background: none;
                text-align: left;
                border: 0;
                color: #fff;
                box-shadow: none;
            }

            &:last-child {
                border-bottom: 0;
            }

            &:after {
                content: "";
                width: 100%;
                height: 100%;
                background: $green-dark;
                position: absolute;
                top: 0;
                left: 0;
                -webkit-clip-path: inset(0 80% 0 0);
                clip-path: inset(0 80% 0 0);
                opacity: 0;
                -webkit-transition: all 2s;
                transition: all 2s;
                z-index: -1;

                @media (max-width: 991.9px) {
                    display: none;
                }
            }

            &:hover {
                color: #fff;
                transition: color 2s;

                @media (max-width: 991.9px) {
                    color: inherit;
                }

                &:after {
                    clip-path: inset(0 0 0 0);
                    opacity: 1;
                }
            }
        }
    }

    &:hover {
        .dropdown-content {
            opacity: 1;
            visibility: visible;

            a {
                margin-top: 0;
                opacity: 1;

                &:first-child {
                    margin-top: 15px;

                    @media (max-width: 991.9px) {
                        margin-top: 0;


                    }
                }
            }
        }
    }
}

.h__inner {

    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.12);
    padding: 5px 0;
    background: #fff;

    .main__bar {
        &:before {
            display: none;
        }

        .logo {
            height: 50px;

            &.logo-v {
                display: none;


            }

            &.logo-h {

                display: block;
                width: 130px;
                height: 50px;
                padding: 5px 0;


            }
        }
    }
}

.h__profile {

    .ico-notify {
        svg {


            path {
                fill: #fff
            }
        }
    }

    .main__bar {
        &:before {
            display: none;
        }

        .menu-icon {
            .navicon {
                background: #fff;

                &:before {
                    background: #fff;
                }

                &:after {
                    background: #fff;
                }
            }
        }

        .logo {
            height: 50px;
        }

        .menu {
            li {
                color: #fff;

                .ico-arr-down {
                    background-image: url(../images/svgs/ico-arr-down-white.svg);
                }
            }
        }
    }

    .logo-green {
        display: none;
    }

    &.fixed__header {
        .menu {
            li {
                color: $black-light;

                .ico-arr-down {
                    background-image: url(../images/pngs/ico-arr-down.png);
                }

                @media (max-width:991.9px) {
                    color: #fff;
                }

            }
        }

        .menu-icon {
            .navicon {
                background: $green-dark;

                &:before {
                    background: $green-dark;
                }

                &:after {
                    background: $green-dark;
                }
            }
        }


        .logo-green {
            display: block;
        }

        .logo-white {
            display: none;
        }
    }



}

.h__inner {


    .user-name {
        color: $green-dark;

    }
}

.loggedin {
    .user-name {
        color: #fff;

    }

    &.fixed__header {
        .user-name {
            color: $green-dark;

        }
    }

    .user-image {
        width: 30px;
        height: 30px;
        @include border-radius(50%);

    }

    .dropdown-menu {
        font-size: 1em;
        right: 0 !important;
        left: auto !important;
        @include border-radius(0);
    }

    .dropdown-toggle {
        display: block;

        &:after {
            display: none;
        }
    }
}

.search {
    float: right;
    margin-top: 9px;


    .searchform {
        position: absolute;
        top: 61px;
        right: 0;
        display: none;
    }

    .fa-close {
        display: none;
    }

    .search-icon {
        display: none;
        display: block;

    }

    &.highlight {
        .searchform {
            display: block;
        }

        .fa-close {
            display: block;
        }

        .search-icon {
            display: none;
        }
    }

    .search-query {
        width: 300px;
        background: #fff;
        @include border-radius(0)
    }
}

.notification-icon {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: red;
    line-height: 18px;
    text-align: center;
    right: -10px;
    font-size: .9em;
    top: -5px;
    overflow: hidden;
}