.cl__breadcrumb {
    position: relative;
    @media (max-width: 767.9px) {
        transform: translateY(-40px);
    }
    @media (max-width: 575.9px) {
        display: none;
    }
}

.uppercontent__wrapper {
    position: absolute;
    left: 0;
    width: 100%;
    z-index: 10;
    &.btn__upload {
        top: 150px;
    }
    &.upper__txt {
        top: 300px;
        @media (max-width: 991.9px) {
            top: 100px;
        }
        @media (max-width: 767.9px) {
            text-align: center;
            top: 200px;
        }
    }
}

.toggle__black {
    @media (max-width: 767.9px) {
        top: 200px;
        color: black;
    }
}

.circle__list__section {
    display: block;
    @media (max-width: 767.9px) {
        padding-top: 100px;
    }
    @media (max-width: 575.9px) {
        padding-top: 170px;
    }
}

.add__padding__align {
    padding: 20px;
    @media (max-width: 575.9px) {
        padding: 5px;
    }
}

#pills-tab {
    .nav-item {
        .nav-link {
            box-shadow: none;
            background-color: white;
            span {
                padding-bottom: 5px;
            }
            &.active {
                span {
                    border-bottom: 2px solid #7FCAAE;
                }
            }
        }
    }
}

#pills-tabContent {
    .add__members__form {
        width: 100%;
        flex-wrap: wrap;
        .input-group-append {
            display: block;
            width: 60%;
            @media (max-width: 575.9px) {
                width: 80%;
            }
        }
    }
    .cl__search__input {
        width: 73.5%;
    }
    .cl__search__icon {
        position: relative;
        width: 40px;
        background: url(../images/pngs/ico-search.png) center no-repeat;
        background-size: 20px;
        cursor: pointer;
        left: -90px;
        top: -5px;
        @media (max-width: 767.9px) {
            display: none;
        }
    }
    .list__members {
        width: 80%;
        @media (max-width: 575.9px) {
            width: 100%;
        }
        .one__item {
            height: 100px;
            overflow: hidden;
            @media (max-width: 767.9px) {
                height: 70px;
            }
        }
        &.scroll__style {
            max-height: 470px;
            padding: 4px;
            overflow-y: scroll;
        }
        .item__member {
            width: 100%;
            @media (max-width: 767.9px) {
                width: 100%;
            }
            .profile-img {
                //   width: 10%;
                //   min-width: 30px;
                img {
                    width: 100%;
                }
            }
            .member__name {
                max-width: 150px;
                @media (max-width: 575.9px) {
                    max-width: 100px;
                }
            }
            .star__list {
                .cl__star {
                    width: 12px;
                    height: 12px;
                    background-image: url(../images/pngs/gray-star.tif.png);
                    background-size: 12px;
                    background-repeat: no-repeat;
                    background-position: center;
                    &.active {
                        background-image: url(../images/pngs/yellow-star.tif.png);
                    }
                    @media (max-width: 575.9px) {
                        width: 10px;
                        height: 10px;
                        background-size: 10px;
                    }
                }
            }
            .remove__wrapper {
                cursor: pointer;
                i {
                    width: 12px;
                    height: 12px;
                    display: inline-block;
                    background-image: url(../images/svgs/ico-remove.tif.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: contain;
                }
            }
        }
    }
    .container-upload-img {
        width: 30px;
        height: 30px;
        @include border-radius(15px);
        background: rgba(30, 137, 145, .13);
        justify-content: center;
        border: 1px dashed $green-light;
        display: flex;
        align-items: center;
        position: relative;
        .cover-account {
            width: 90%;
            height: 90%;
            position: absolute;
            left: 0;
            top: 0;
            text-indent: 10000px;
            cursor: pointer;
            background: transparent;
            border: none;
        }
        .upload {
            color: $green-light;
            font-size: 2rem;
            font-weight: bold;
        }
    }
}


/* Scroll Bar Styling for first tab of the pills */

.scroll__style::-webkit-scrollbar {
    width: 4px;
    @media (max-width: 575.9px) {
        width: 0;
    }
}


/* Track */

.scroll__style::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.scroll__style::-webkit-scrollbar-thumb {
    background: #07979D;
}


/* Handle on hover */

.scroll__style::-webkit-scrollbar-thumb:hover {
    background: #23757B;
}

.cursor__pointer {
    cursor: pointer;
}