.sign-in-up__wrapper {
  background: #edf6f7;
}

.signup-tabs {
  border-bottom: 0;
  margin-bottom: 15px;

  .nav-link {
    width: 120px;
    padding: 0.7em 1.1em;
    border-radius: 20px;
    color: $green-light;

    &.active {
      background: $main-gradient;
      color: $white;
    }
  }
}

.left-part {
  height: inherit;

  @media (max-width: 991.9px) {
    display: none;
  }

  &.cover-custom {
    @media (max-width: 991.9px) {
      display: block;
    }

    @media (max-width: 767.9px) {
      display: none;
    }
  }

  .bg__sign-in-up {
    background: url(../images/pngs/bg-icons.png) no-repeat center;
    background-size: 65%;

    img {
      width: 73%;
      margin: 0 auto;
      display: block;
    }
  }
}

.sign-in-up__logo {
  padding: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 125px;
  z-index: 20;
}

.right-part {
  background: $white;

  @media (max-width: 991.9px) {
    padding-top: 80px;
  }
}

.content-part {
  h5 {
    color: $green-dark;

    @media (max-width: 575.9px) {
      font-size: 1.8em;
    }
  }

  h6 {
    color: $green-extra-dark;
    margin-bottom: 2em;
  }
}

.btns__sign-in-ups {
  margin-bottom: 20px;

  .btn {
    width: 32%;
    margin-right: 1%;
    white-space: nowrap;
    font-size: 0.8em;
    padding: 7px 8px;
    position: relative;

    @media (max-width: 1199.9px) and (min-width: 992px) {
      width: 180px;
      margin-right: 1%;
      margin-bottom: 10px;
    }

    @media (max-width: 575.9px) {
      width: 160px;
      margin-right: 1%;
      margin-bottom: 10px;
    }

    i {
      display: inline-block;
      position: absolute;
      left: 8px;
      top: 8px;
    }

    &.btn__fb {
      background: #3c5a99;
      color: #fff;

      .ico-fb {
        width: 15px;
        height: 15px;
        background: url(../images/pngs/ico-fb.png) no-repeat;
        background-size: contain;
      }
    }

    &.btn__tw {
      background: #49c9f2;
      color: #fff;

      .ico-tw {
        width: 20px;
        height: 20px;

        background: url(../images/pngs/ico-tw.png) no-repeat;
        background-size: contain;
        top: 6px;
      }
    }

    &.btn__google {
      border: 1px solid #707070;
      background: #fff;
      color: #000;

      .ico-google {
        width: 15px;
        height: 15px;
        background: url(../images/pngs/ico-google.png) no-repeat;
        background-size: contain;
      }
    }

    &.btn__linked {
      background: #0077b5;
      color: #fff;

      .ico-linked {
        width: 20px;
        height: 15px;

        background: url(../images/svgs/linkedin.svg) no-repeat;
        background-size: contain;
        top: 6px;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.option__span {
  margin-bottom: 2em;

  span {
    color: #000;
  }
}

form {
  text-align: left;

  label {
    color: #000;
    font-size: 1.1em;
    font-weight: bold;
  }

  .form-text {
    color: rgba(0, 0, 0, 0.4);
    font-size: 0.9em;
  }

  .form-check {
    label {
      color: rgba(0, 0, 0, 0.4);
      font-size: 0.9em;
      font-family: "Raleway-Medium";
    }
  }

  .link-forget {
    a {
      color: rgba(0, 0, 0, 0.4);
      font-family: "Raleway-Medium";
      font-size: 0.9em;
    }
  }
}

.log-in {
  .btn {
    width: 130px;
    height: 50px;
    @include border-radius(20px);
    box-shadow: 0px 5px 25px 0px rgba(31, 135, 141, 0.66);

    &.btn-resend {
      width: 185px;
    }
  }
}

.question {
  margin: 40px 0 20px;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    width: 100%;
    height: 1px;
    background: rgba(112, 112, 112, 0.28);
    z-index: 1;
  }

  span {
    background: $white;
    padding: 10px;
    color: $green-extra-dark;
    z-index: 2;
    position: relative;
  }
}

.sign-up {
  .btn {
    width: 130px;
    height: 45px;
    @include border-radius(20px);
    color: $green-dark;
  }
}

.py-up {
  padding-top: 100px;
  padding-bottom: 50px;

  @media (max-width: 1199.9px) and (min-width: 992px) {
    padding: 30px 0;
  }
}

.form-control {
  background: #f6f6f6;
  border: 0;
  font-size: 1em;
  position: relative;

  &.datepicker {
    padding-right: 30px;
    background-image: url(../images/svgs/ico-calendar.svg);
    background-repeat: no-repeat;
    background-position: right 20px center;
    background-size: 15px;
  }
}

textarea {
  height: 120px !important;
}

.invalid-feedback {
  font-size: 1em;
  color: darkred;
}

.dz-success-mark,
.dz-details,
.dz-error-message,
.dz-error-mark {
  display: none;
}

.container-cover-img {
  width: 100%;
  height: 120px;
  max-height: 120px;

  @include border-radius(0.5rem);
  background: rgba(30, 137, 145, 0.13);
  justify-content: center;
  border: 1px dashed $green-light;

  display: flex;
  align-items: center;
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;

  input {
    //background: rgba(30, 137, 145, .13) !important;
  }

  .cover-account {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    text-indent: 10000px;
    background: transparent;
  }

  .upload {
    color: #93a4b6;
    font-size: 0.9em;
  }

  &.dz-started {
    height: 100%;

    span.upload {
      display: none;
    }
  }
}

.dz-remove {
  position: absolute;
  top: 0;
  right: 20px;
  width: 20px;
  height: 20px;
  @include border-radius(50%);
  background: $green-light;
  color: #fff;
  text-align: center;
  font-size: 0.8em;
  line-height: 20px;
}

.profile-img-container {
  width: 100px;
  height: 100px;
  @include border-radius(50%);
  background: #e1f0f1;
  border: 5px solid rgba(31, 135, 141, 0.5);
  margin: 0 auto;
  position: relative;

  .ico-upload {
    width: 30px;
    height: 30px;
    right: -15px;
    position: absolute;

    top: 35px;
    z-index: 10;

    width: 30px;
    height: 30px;
    @include border-radius(50%);
    background: url(../images/pngs/ico-cam.png) no-repeat;
    background-size: contain;
    display: inline-block;
    text-indent: 10000px;
  }
}

.dropzone-previews {
  width: 100%;
  height: 100%;
  @include border-radius(50%);
  position: relative;

  img {
    @include border-radius(50%);
  }

  .dz-remove {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    @include border-radius(50%);
    background: $green-light;
    color: #fff;
    text-align: center;
    font-size: 0.8em;
    line-height: 20px;
  }

  .dz-preview {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.upload-btn {
  .btn {
    width: 200px;
    height: 40px;
    color: $green-dark;
    border: 2px solid $green-dark;
    font-weight: bold;
    @include border-radius(20px);
  }
}

.custom-radio {
  padding-left: 1.5rem;

  .custom-control-label {
    &:after,
    &:before {
      left: -1.5rem;
      top: 0.225rem;
    }
  }
}

.custom-switch .custom-control-label::before {
  height: 17px;
  width: 30px;
  border-radius: 15px;
}

.custom-switch .custom-control-label::after {
  width: 13px;
  height: 13px;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background: linear-gradient(#1f8a91 0%, #44a394 53.27%, #62b796 100%);
}

.custom-switch .custom-control-input:checked ~ .custom-control-label::after {
  background-color: #fff;
  left: calc(-3.25rem + 4px);
}

select.form-control {
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  background-image: url(../images/svgs/arr-select.svg);
  background-repeat: no-repeat;
  background-position: right 20px center;
  background-size: 10px;
  padding-right: 30px;
}

option {
  line-height: 40px;
}

.company__info {
  max-height: 90vh;
  overflow-y: scroll;
  padding: 0 20px;
  overflow-x: hidden;
}

.add-file {
  width: 80%;
  height: 50px;
  margin-bottom: 30px;
}

.datepicker-dropdown {
  font-size: 1rem;
}

.bg-thank {
  background: url(../images/pngs/bg-thankupop.png) no-repeat;
  background-size: cover;
}

.info {
  font-size: 0.9em;

  .ico-info {
    width: 17px;
    height: 17px;
    background: url(../images/pngs/ico-info.png) no-repeat;
    display: inline-block;
    background-size: 100%;
    margin-right: 5px;
  }
}
