.job-desc {
    .block-part {
        padding-bottom: 20px;
        border-bottom: 1px solid #dee2e6;
        margin-bottom: 20px;

        &:last-child {
            padding-bottom: 0;
            margin-bottom: 0;
            border: 0;
        }

        h6 {
            margin-bottom: 20px;
        }

        .container-cover-img {
            height: 85px;
        }
    }
}

.box-style {
    padding: 25px 20px;
    border: 1px solid #E4F3F8;
    background: #F9FCFD;
    border-radius: 20px;

    @media (max-width: 575.9px) {
        padding: 25px 10px;


    }

}

.border-right {

    @media (max-width: 767.9px) {
        border: 0 !important;
    }
}

.jd-profile-img {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    @include border-radius(50%);
    overflow: hidden;
    border: 1px solid rgba(227, 227, 227, .66);

    img {
        width: 100%;
        min-height: 100%;
        @include border-radius(50%);
    }
}


.more-persons {
    color: $green-dark;
    font-weight: bold;

    span {
        border-bottom: 1px solid $green-dark;
    }
}

.job__prop {
    .view-full {
        .data-freelancer {
            margin-left: 130px;

            @media (max-width: 575.9px) {
                margin-left: 0;
            }
        }
    }

    .view-profile {
        .btn {
            margin-right: 10px;

            @media (max-width: 575.9px) {
                width: 100px;
            }

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

.ico-status {
    display: inline-block;
    width: 8px;
    height: 8px;
    @include border-radius(50%);
    background: #F3E741;
    margin-right: 10px;

}

.btn-collapse {
    color: #A4AAB8;
    font-family: 'Raleway-Bold';
    font-size: 1.1em;

    .ico-arrow-green {
        width: 20px;
        height: 20px;
        display: inline-block;
        border: 1px solid $green-dark;
        background: url(../images/pngs/ico-arr-down-green.png) no-repeat center;
        background-size: 10px;
        vertical-align: middle;
    }



}

.data-history {
    flex: 0 0 67%;

    ul {
        padding-left: 30px;

        li {
            position: relative;
            padding-left: 20px;
            opacity: 0.3;



            .time {
                color: #A4AAB8;
                font-family: 'Raleway-Medium';
                margin-bottom: 5px;
            }

            p {
                margin-bottom: 5px;
                font-size: .8em;

            }

            &:after {
                content: "";
                position: absolute;
                top: 5px;
                left: 0;
                width: 12px;
                height: 12px;
                border: 2px solid $green-dark;
                @include border-radius(50%);
            }

            &:before {
                content: "";
                position: absolute;
                top: 23px;
                left: 5px;
                width: 1px;
                height: 95%;
                background: #D1D1D1;
            }

            &:nth-child(1) {
                opacity: 1;

                p {
                    font-size: 1em;
                }


            }

            &:last-child {
                &:before {
                    display: none;
                }
            }
        }
    }

    .info__item {
        span {
            width: 50%;
        }
    }
}


.modal {
    form {
        padding: 0 40px 0 20px;
    }

    .modal-header {
        border: 0;
    }

    .modal-footer {
        border: 0
    }

    label {
        font-weight: normal;
    }

    .form-control {
        border: 1px solid rgba(112, 112, 112, 0.28);
        background: #fff;
    }

    .input-group-text {
        width: 100px;
        color: #fff;
        text-align: center;
        justify-content: center;
        font-size: .8em;
        background: linear-gradient(#4b9fa3 0%, #62b796 100%);
    }

    .btn {
        width: 120px;
        height: 43px;
        @include border-radius(20px)
    }
}


/*====================================Job Description ============================================*/


.heading__page {
    span {
        color: #767672;
        font-size: 1.1em;

        .ico-share {
            background-image: url(../images/svgs/ico-material-share.svg);
            background-size: contain;
            background-repeat: no-repeat;
            display: inline-block;
            width: 25px;
            height: 25px;
            margin-right: 5px;
        }
    }
}

.green-points {
    li {
        span {
            i {
                background: $green-light;
                border-radius: 50%;


            }
        }
    }
}

.job-skills {
    li {
        margin-bottom: 20px;
    }
}



.btn-add-circle {
    width: 30px;
    height: 30px;
    padding: 0;

    .ico-plus-button {
        background-image: url(../images/svgs/ico-plus.svg);
        width: 15px;
        height: 30px;
        background-size: contain;
        background-repeat: no-repeat;
        display: inline-block;
        background-position: center;
    }
}


.jd-left {
    .btn {
        width: 20px;
        height: 20px;
        background-color: #F44336;
        margin-right: 10px;
        padding: 0;
        flex: 0 0 20px;


        .ico-x-button {
            background-image: url(../images/svgs/ico-x-white.svg);
            width: 8px;
            height: 20px;
            background-size: contain;
            background-repeat: no-repeat;
            display: inline-block;
            background-position: center;
        }

        &.send-proposal {
            width: 170px;
            height: 45px;
            box-shadow: none;
        }
    }

    .jd-freelance-img {
        width: 50px;
        height: 50px;
        @include border-radius(50%);
        border: 1px solid #E4F3F8;
        flex: 0 0 50px;

        img {
            width: 100%;
            @include border-radius(50%);
        }
    }

    .jd-name {
        margin-left: 10px;


    }
}

.attach {
    span {
        padding-right: 15px;
        background: url(../images/pngs/ico-attach.png)no-repeat center right;
        background-size: 10px;
    }
}

.input-group-append {
    span {
        cursor: auto;
    }
}

.name__item {
    a {
        color: inherit;
    }
}