.bord-green{
    border: 1px solid green;
}

.bord-black{
    border: 1px solid black;
}

.bord-blue{
    border: 1px solid blue;
}

.bord-red{
    border: 1px solid red;
}

.ask-question{

    .img__wrapper{

        img{
            height: 460px;
            position: relative;
            top: 20px;
            left: 95px;

            @media (max-width: 991.9px) {
                height: 300px;
                top: 100px;
                left: 20px;  
            }

            @media (max-width: 767.9px) {
                height: 300px;
                top: 0;
                left: 0;

            }

            @media (max-width: 575.9px) {
                height: 150px;
            }
        }
    }

    &.add__bg {
        background-image: url(../images/pngs/Component3.png);
        background-size: 2000px;
        background-repeat: no-repeat;
        background-position: -150px -200px;
        height: 800px;
        padding-top: 50px;

        &:before {
            content: "";
            position: absolute;
            top: 230px;
            left: 310px;
            height: 90px;
            width: 90px;
            background: url(../images/pngs/ico-bg.png) no-repeat;
            background-size: 50%;

        }
    }

    
}



