.payment {
    .Tabs__details {
        .tab-content {
            background: transparent;
            border: 0;
        }
    }


    .table-payment {
        thead {
            th {

                border-top: 0;
                color: $green-dark;
                font-family: 'Raleway-Bold';
            }
        }

    }
}





.box-table-green {
    background: #F9FCFD;
    @include border-radius(20px);
    padding: 20px;

    thead {
        background: $green-dark;

        th {
            color: #fff !important;

            &:first-child {
                @include border-top-left-radius(20px);

            }

            &:last-child {
                @include border-top-right-radius(20px);

            }


        }
    }



}

.table-payment {

    th,
    td {
        vertical-align: middle !important;


        font-size: .8em;

    }
}

.form-table {
    .form-control {
        width: 130px !important;
    }
}