.axis-cover {
    position: relative;
    min-height: 135vh;
    .cover-left {
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: url(../images/pngs/bg-left.png)no-repeat left center;
    }
    &.questions {
        .cover__page {
            width: 40%;
            background-image: url(../images/pngs/bg-question.png);
            @media (max-width: 575.9px) {
                width: 65%;
            }
        }
        .person__cover {
            background: url(../images/pngs/person-question.png) no-repeat left top 35px;
            background-size: 100%;
            width: 75%;
            margin: 0 5%;
        }
    }
}

.main__question {
    .disc-data {
        .comment {
            background: $green-light;
            color: #fff;
        }
    }
}

.answers {
    margin-left: 50px;
    @media (max-width: 575.9px) {
        margin-left: 0;
    }
    .btn {
        width: 115px;
        height: 37px;
        @include border-radius(20px)
    }
}

.desciption-question {
    background: #F1F2F2;
    padding: 20px 0 20px 20px;
    @include border-radius(20px);
    .h-limit {
        max-height: 300px;
        overflow-y: scroll;
        overflow-x: hidden;
        padding-right: 20px;
    }
}

.saved-action {
    a {
        span {
            width: 40px;
            height: 40px;
            @include border-radius(50%);
            background-color: $green-light;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .ico-saved {
            width: 20px;
            height: 20px;
            background: url(../images/pngs/ico-saved.png)no-repeat center;
            background-size: contain;
            display: inline-block;
        }
        &.active,
        &:hover {
            span {
                background-color: $green-dark;
            }
        }
    }
}

.circles {
    .cover__page {
        .person__cover {
            background: url(../images/pngs/bg-circles.png) no-repeat left top 35px;
            background-size: 100%;
            width: 75%;
            margin: 0 5%;
        }
    }
}

.btn-createcircle {
    @include border-radius(20px);
    height: 40px;
    line-height: 30px;
    .ico-plus {
        width: 10px;
        height: 10px;
        background: url(../images/pngs/ico-plus.png)no-repeat;
        background-size: contain;
        display: inline-block;
    }
}

.circles__list {
    .item {
        width: 32%;
        display: inline-block;
        margin-bottom: 40px;
        @media (max-width: 991.9px) {
            width: 49%;
        }
        @media (max-width:575.9px) {
            width: 100%;
        }
    }
    .img__item {
        width: 100px;
        height: 100px;
        @include border-radius(50%);
        position: relative;
        overflow: hidden;
        box-shadow: 0px 6px 20px rgbargba(129, 128, 128, 0.4);
        margin-bottom: 15px;
        img {
            width: inherit;
            height: inherit;
            @include border-radius(50%);
        }
        .date-create {
            position: absolute;
            bottom: 0px;
            width: 100%;
            height: 30px;
            text-align: center;
            background: rgba(0, 0, 0, .8);
            font-size: .9em;
            line-height: 30px;
            left: 0;
            @include border-bottom-left-radius(50%);
            @include border-bottom-right-radius(50%);
            color: #fff;
        }
    }
    .circle-hint {
        color: #6D6E71;
        font-weight: .9em;
        font-family: 'Raleway-Light';
        width: 60%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-bottom: 10px;
    }
    .btn {
        @include border-radius(15px);
        height: 30px;
        width: 75px;
    }
}


/****************************FAQ*****************************/

.question {
    &:after {
        display: none;
    }
    .question-header {
        h4 {
            font-size: 1.2em;
            font-weight: bold;
        }
    }
}